<template>
    <div>
        <div class='bread'>
            <strong></strong>
        </div>
        <el-form :model="filecontent" ref="ruleForm" label-width="100px" class="demo-ruleForm"
                 style="margin:20px;width:80%;min-width:800px;">
            <template v-for="(obj ,index) in $dc.profile.projectPath">
                <el-form-item :label="''+(index+1)" :key="index">
                    <div style="padding-left: 5px">
                        <el-row :gutter="10">
                            <el-col :span="3" style="text-align: right">
                                :
                            </el-col>
                            <el-col :span="10">
                                <el-input v-model="obj.key" placeholder=""></el-input>
                            </el-col>
                        </el-row>
                        <el-row :gutter="10" style="margin-top: 5px;">
                            <el-col :span="3" style="text-align: right">
                                :
                            </el-col>
                            <el-col :span="18">
                                <el-input v-model="obj.value" placeholder=""></el-input>
                            </el-col>
                            <el-col :span="2">
                                <el-button type="danger" icon='delete' size="mini"
                                           @click='deleteParam(obj,index,projectPathArray)'>
                                </el-button>
                            </el-col>
                        </el-row>
                    </div>
                </el-form-item>
            </template>
            <el-form-item>
                <el-button @click="addParam"></el-button>
                <el-button type="primary" @click="saveFile"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import profileApi from '../../../api/profile';
    import forEach from 'lodash/forEach';
    export default {
        name: 'project-path',
        methods: {
            deleteParam(row, index, list) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$dc.profile.projectPath.splice(index, 1);
                });
            },
            async saveFile() {
                // host
                let response = await profileApi.saveFile(this.$dc.profile);
                let serverData = response.data;
                if (serverData.code == 0) {
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                } else {
                    this.$message.error(`${serverData.msg}`);
                }
            },
            addParam() {
                this.$dc.profile.projectPath.push({
                    key: "",
                    value: ""
                });
            }
        }
    };

</script>



