<template>
  <div>
    <div class='bread'>
      <strong></strong>
    </div>
    <el-form :model="filecontent" ref="ruleForm" label-width="250px" class="demo-ruleForm"
             style="margin:20px;width:80%;min-width:800px;">
      <el-form-item label="">
        <el-input v-model="$dc.configure.proxyPort" placeholder="proxy"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="$dc.configure.requestTimeoutTime"  placeholder="proxy"></el-input>
      </el-form-item>
      <el-form-item label="gitlab token">
        <el-input v-model="$dc.configure.gitlabToken" placeholder="gitlabtoken (proxygitlabtoken)"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="saveFile"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import confApi from '../../../api/conf';
  import forEach from 'lodash/forEach';
  export default {
    name: 'configure',
    methods: {
      async saveFile() {
        this.$dc.configure.requestTimeoutTime = parseInt(this.$dc.configure.requestTimeoutTime);
        this.$dc.configure.proxyPort = parseInt(this.$dc.configure.proxyPort);
        let response = await confApi.saveFile(this.$dc.configure);
          var serverData = response.data;
          if (serverData.code == 0) {
              this.$message({
                  type: 'success',
                  message: '!'
              });
          } else {
              this.$message.error(`${serverData.msg}`);
          }
      }
    }
  }

</script>



